.header-inner {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background: transparent;
  padding: $spacer * 0.75 3rem;
}

.header-nav {
  display: flex;
  align-items: center;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;

  .nav-item .nav-text {
    color: $slate-gray;
    display: inline-block;
    padding: $nav-link-padding-y $navbar-nav-link-padding-x;
  }

  .nav-link {
    color: $primary;
    padding-right: $navbar-nav-link-padding-x;
    padding-left: $navbar-nav-link-padding-x;

    &:hover,
    &:focus {
      color: darken($primary, 15%);
    }

    &.disabled {
      color: $gray-400
    }
  }

  .nav-link {
    .iap-icon {
      background: $primary;
    }

    &:hover,
    &:focus {
      .iap-icon {
        background: darken($primary, 15%);
      }
    }
  }

  .nav-link-with-avatar {
    padding-top: $spacer * (2.5 - 2.375) * .5;
    padding-bottom: $spacer * (2.5 - 2.375) * .5;
  }
}
